body {
    margin: auto;
    max-width: 1100px;
    background-color: black;
    color: white;
    text-align: center;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    height: 100vh;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

button {
    background-color: black;
    margin: auto;
}

.video_Container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    margin: 5vh auto;
    background-color: rgba(20, 20, 20, 0.11);
    border-radius: 20px;
}

.videoSize {
    width: 800px;
}

.switchArrow {
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 50px;
    background-color: rgb(240, 24, 0);
    border: 3px rgb(255, 255, 255) solid;
    margin: 0 auto;
    text-align: center;
    color: white;
    border-radius: 10px;
    transition: ease-in-out 200ms;
}

.switchArrow:hover {
    transform: scale(1.2);
}

@media only screen and (max-width: 962px) {
    .videoSize {
        width: 280px;
    }

    .switchArrow {
        width: 20px;
    }

    .video_Container {
        flex-direction: column;
    }
}

@media only screen and (max-width: 350px) {
    .switchArrow {
        width: 100%;
        flex-direction: column;
    }

    button {
        width: 280px;
        margin: 5px auto;
    }

    .videoSize {
        height: 350px;
    }

    .switchArrow:hover {
        transform: scale(1.1);
    }
}